#masthead-banner {
  text-decoration: none;
  color: #ffffff;
  background: #262626;
  border-top: 3px solid #262626;
  border-left: 3px solid #262626;
  border-right: 3px solid #262626;
  border-bottom: 3px solid #00bab6;
  box-sizing: border-box; }
  #masthead-banner *,
  #masthead-banner *::before,
  #masthead-banner *::after {
    box-sizing: inherit; }
  #masthead-banner #masthead-banner-content {
    box-sizing: border-box; }
  #masthead-banner:hover {
    background: #161616;
    border-color: transparent;
    border-bottom: 3px solid #00bab6; }
  #masthead-banner:focus {
    border: 3px solid #00bab6;
    outline: none; }
    #masthead-banner:focus #masthead-banner-content {
      outline: 1px solid #ffffff; }
  #masthead-banner .masthead-banner--align-center {
    align-items: center; }
  #masthead-banner .masthead-banner--space-between {
    justify-content: space-between; }
  #masthead-banner .masthead-banner--flex {
    display: flex; }
  #masthead-banner .masthead-banner__icon {
    margin: 0 24px; }

#masthead-banner-desktop {
  height: 75px;
  display: flex;
  align-items: center;
  justify-content: space-between; }

#masthead-banner-mobile {
  height: 75px; }

@media (max-width: 659px) {
  #masthead-banner-desktop {
    display: none; }
  #masthead-banner-mobile {
    display: flex; } }

@media (min-width: 660px) {
  #masthead-banner-desktop {
    display: flex; }
  #masthead-banner-mobile {
    display: none; } }

#think-banner-link {
