    #think-banner-link #think-banner-content .video-background {
      width: auto;
      height: auto;
      min-width: 100%;
      min-height: 100%;
      position: absolute;
      top: 50%;
      left: 65%;
      transform: translateY(-50%) translateX(-65%);
      z-index: 0;
      display: none; }
      @media (min-width: 1000px) {
