      #think-banner-link #think-banner-content .video-background video {
        width: 50%;
        height: 100%;
        -o-object-fit: cover;
           object-fit: cover;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateY(-50%) translateX(-50%); }
    #think-banner-link #think-banner-content .align-center {
      align-items: center; }
    #think-banner-link #think-banner-content .space-between {
      justify-content: space-between;
